$imgWidth: 492px;
$imgHeight: 167px;

@import "../../../../css/module/color";
@import "../../../../css/module/fn";

.imgBlock {
	display: inline-block;
	margin: 10px;
	width: $imgWidth;
	height: $imgHeight;
	line-height: $imgHeight;
	border-radius: 20px;
	
	border: 8px solid white;
	background: {
		//color: #eee;
		size: 100% 100%;
		repeat: no-repeat;
	}
	
	a {
		display: block;
		line-height: $imgHeight;
		width: 100%;
		height: 100%;
		text-align: center;
		font-size: 26px;
		font-weight: 900;
		color: #fff;

	}
	
}

#ulBuyerList , .ulBuyerList {
	> li {
		display: inline-block;
		line-height: 1.5em;
		border: 1px solid #eee;
		margin: 5px;
		padding: 1em;
		vertical-align: top;
		
		@include shadow();
		@include textClip();
		text-align: center;
		color: #888;
		background: {
			color: #fff;
		}
	}
}

#divRegionList{
	> .ulBuyerList{
		margin-bottom: 20px;
	}
}

#divList {
	> h1 {
		//color: $red;
		margin: 0.5em 0;
	}
	
	> .ulList{
		margin-bottom: 20px;
		
		> li {
			display: inline-block;
			width: 160px;
			height: 160px;
			border: 1px solid #eee;
			margin: 5px;
			padding: 1em;
			vertical-align: top;
			
			@include shadow();
			@include textClip();
			text-align: center;
			color: #888;
			cursor: pointer;
			
			background: {
				color: #fff;
				repeat : no-repeat;
				position: 2px 2px;
			}
			
			>.ico-add{
				display: block;
				margin: -5px;
				background: {
					repeat: no-repeat;
					position: center center;
					size: 100% auto;
				}
			;
			}
			
		}
		
	}

}

.smallicon {
	border:0;
	width:25px;
	height:25px;
}


.mytext {
	font-size:26px;
	font-weight: bold;
	color:#c7140d;
}

.mytext2 {
	margin-left: 22px;
	font-size:10px;
	font-weight: bold;
	color: #fff;
	background-color: #e92d27;
	border-radius: 5px;
	word-wrap: break-word;
	width:90%;
}

